@charset "UTF-8";
body {
  font-family: 'Roboto', sans-serif;
  font-size: 14pt;
  color: #1F1F1F;
  background-color: #EEEEEE; }

.page-header {
  padding-left: 16px;
  padding-right: 16px;
  box-shadow: 0px 5px 38px 0px rgba(0, 0, 0, 0.3);
  padding-bottom: 1px;
  padding-top: 5px;
  background-color: #00BBD3;
  color: #fff; }
  .page-header__header {
    margin-top: 0px; }

@media (min-width: 375px) {
  .page-header__header {
    max-width: 350px;
    margin-left: auto;
    margin-right: auto; } }

.main-app {
  padding-left: 16px;
  padding-right: 16px;
  padding-top: 15px;
  padding-bottom: 15px; }

@media (min-width: 375px) {
  .main-app {
    max-width: 350px;
    margin-left: auto;
    margin-right: auto; } }

.new-task__container {
  text-align: center; }

.new-task__input {
  margin-top: 20px;
  padding: 5px;
  background-color: #fafafa;
  border: none;
  box-shadow: 0px 5px 38px 0px rgba(0, 0, 0, 0.3);
  outline-color: #00BBD3; }

.new-task__btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  vertical-align: middle;
  margin-left: 15px;
  background-color: #92CBC5;
  cursor: pointer;
  box-shadow: 0px 5px 38px 0px rgba(0, 0, 0, 0.3);
  color: #fff; }
  .new-task__btn:hover, .new-task__btn:focus {
    background-color: #6fbbb3; }

.task {
  padding: 5px;
  background-color: #fafafa;
  box-shadow: 0px 5px 38px 0px rgba(0, 0, 0, 0.3);
  margin-bottom: 15px; }
  .task__list {
    margin: 0;
    margin-top: 40px;
    padding: 0;
    list-style: none; }
  .task__index {
    vertical-align: middle; }
  .task__text {
    vertical-align: middle; }
  .task__checkbox {
    display: none; }
    .task__checkbox:checked ~ .task__custom-checkbox {
      border-color: #92CBC5;
      background-color: #92CBC5; }
    .task__checkbox:checked ~ .task__text,
    .task__checkbox:checked ~ .task__index {
      text-decoration: line-through;
      color: #858585; }
  .task__custom-checkbox {
    display: inline-block;
    width: 15px;
    height: 15px;
    border: 2px solid #a2a2a2;
    vertical-align: middle;
    border-radius: 3px;
    margin-left: 10px; }

@media (min-width: 375px) {
  .task {
    max-width: 333px; } }

.filters {
  display: flex;
  justify-content: space-between; }

.index-filter__btn {
  vertical-align: top;
  background-color: transparent;
  border: none;
  font-weight: 700;
  position: relative;
  color: #858585;
  cursor: pointer;
  outline-color: #00BBD3; }
  .index-filter__btn:after {
    content: '↑';
    display: inline-block;
    vertical-align: top;
    width: 15px;
    height: 25px;
    position: absolute;
    top: 0px; }
  .index-filter__btn:hover {
    color: #1F1F1F; }
    .index-filter__btn:hover:after {
      color: #1F1F1F; }

.index-filter__btn_reverse:after {
  transform: rotate(180deg); }

.name-filter__btn {
  vertical-align: top;
  background-color: transparent;
  border: none;
  font-weight: 700;
  position: relative;
  color: #858585;
  cursor: pointer;
  outline-color: #00BBD3; }
  .name-filter__btn:hover, .name-filter__btn:focus {
    color: #1F1F1F; }

.page-footer {
  padding-left: 16px;
  padding-right: 16px; }
  .page-footer__inner {
    display: flex;
    justify-content: space-between;
    margin-top: 40px;
    padding: 5px 15px;
    background-color: #fafafa;
    box-shadow: 0px 5px 38px 0px rgba(0, 0, 0, 0.3); }

@media (min-width: 375px) {
  .page-footer__inner {
    max-width: 350px;
    margin-left: auto;
    margin-right: auto; } }

.remove-btn {
  vertical-align: top;
  background-color: transparent;
  border: none;
  font-weight: 700;
  position: relative;
  color: #858585;
  cursor: pointer; }
  .remove-btn__icon {
    position: absolute;
    left: -20px;
    fill: #858585; }
  .remove-btn:hover, .remove-btn:focus {
    color: #1F1F1F; }
    .remove-btn:hover .remove-btn__icon, .remove-btn:focus .remove-btn__icon {
      fill: #1F1F1F; }
